import * as React from "react";
import { Link } from 'react-router-dom';

import './Navigation.sass';
import Emoji from "../Emoji";

export class Navigation extends React.Component {

    get renderLinks() {
        return [
            {link:'/', label:'home', emoji: <Emoji name='trex' /> },
            {link:'/projects', label:'projects', emoji: <Emoji name='satellite' />},
            {link:'/career', label:'career', emoji: <Emoji name='technologist' />},
        ].map( (x,i)=>(
            <Link to={x.link} key={i}>
                <div className='navLink'>
                    <span>{x.emoji}</span> 
                    <span>{x.label}</span>
                </div>
            </Link>
        ))
    }

    render(){
        return (
            <div id="nav-container">
                {this.renderLinks}
            </div>
        );
    }
}




