import React, { Component } from 'react';

import './Home.sass'

class HomeComponent extends Component {
  
  render() {
    return (
      <div className="main-container home">
          <h1>/idp</h1>
          <h3>Welcome!</h3>

          <center>
            <img alt="Igor de Paula" src='/img/idp_work.jpg' style={{borderRadius:'4px', margin:'20px 0', height:'175px'}} />
          </center>

          <p>
            My name is <b>Igor</b> de Paula, Im a technologist, a builder, a student and an entrepreneur. 
            With almost <b>6 years of software engineering experience</b> Im proud to say Ive always positively impacted
            the companies Ive worked on through my <b>diligent work and positive energy</b>.
          </p>
          <p>
            I have not held a formal job and have been working on myself and personal projects for a few months. 
            So, <b>Im eager to bring everything I learned into a challenging and rewarding environment</b> 
            doing work that not only delivers on customer expectations and business goals but also 
            facilitates my teammates work and contributes to the growth of the company.
          </p>
          <p>
            I hold my work to a very <b>high standard</b> and Im always <b>looking to improve and innovate</b> in every opportunity I have.
          </p>


          <h3>Quick links</h3>
          <ul>
            <li>- <a href="mailto:igordepaula1@gmail.com?subject=Hi Igor! ">Email</a></li>
            <li>- <a href="/files/IgordePaula-resume.pdf" target="_blank" rel="noopener noreferrer">Resume</a></li>
            <li>- <a href="https://github.com/igorp1" target="_blank" rel="noopener noreferrer">GitHub</a></li>
          </ul>

      </div>
    );
  }
}

export const Home = HomeComponent // AnimatedWrapper(HomeComponent);



