import React from 'react'

// dimension : 30x30 
// encoder : http://b64.io

const emojiPolyfillImages = {
    'tada' :        'data:image/png;base64,',
    'smile' :       'data:image/png;base64,',
    'rocket' :      'data:image/png;base64,',
    'trex' :        'data:image/png;base64,',
    'satellite' :   'data:image/png;base64,',
    'technologist': 'data:image/png;base64,',
    'brain' :       'data:image/png;base64,',
    'envelope' :    'data:image/png;base64,',
    'crayon':       'data:image/png;base64,',
    'chart':        'data:image/png;base64,',
    'computer' :    'data:image/png;base64,',
    'rainbow' :     'data:image/png;base64,',
    'station' :     'data:image/png;base64,',
    'bulb' :        'data:image/png;base64,',
    'microscope' :  'data:image/png;base64,',
    'houses' :      'data:image/png;base64,',
    'tokyo' :       'data:image/png;base64,'
} 

const Emoji = ({name, size, c, space}) => (
    <div style={{display:'inline-block', height:(size || '1.1em'), verticalAlign:'middle', marginLeft:'.2em', marginRight:'.2em'}}>
        {[ ...new Array(c || 1)].map((x,i)=><img alt={`${name} emoji`} key={i} src={emojiPolyfillImages[name]} height='100%' />)}
    </div>
)

export default Emoji



